@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;500;700&display=swap);
/* width */

::-webkit-scrollbar {
    width: 8px;
}


/* Track */

 ::-webkit-scrollbar-track {
    background-color: #f1f1f1;
}


/* Handle */

 ::-webkit-scrollbar-thumb {
    background-color: #888;
}


/* Handle on hover */

 ::-webkit-scrollbar-thumb:hover {
    background-color: #6e6e6e;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.clickable:hover {
    -webkit-filter: brightness(80%);
            filter: brightness(80%);
    cursor: pointer;
}
body {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: whitesmoke;
}


/* HEADER */

#root>.header {
    background-color: rgb(235 235 255);
    padding: 60px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    grid-gap: 20px;
    gap: 20px;
    -webkit-align-items: center;
            align-items: center;
}

#root>.header>button {
    background-color: rgb(49, 120, 212);
    color: white;
    padding: 10px 50px;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    font-size: 18px;
}

#root>main {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    grid-gap: 20px;
    gap: 20px;
    padding: 40px;
    min-height: calc(100vh - 2*60px - 48px - 3*20px - 20px - 1px);
}

.card-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-gap: 40px;
    gap: 40px;
}

.card {
    background-color: white;
    width: 250px;
    min-width: 250px;
    max-width: 250px;
    height: 200px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-flex: 1 1;
            flex: 1 1;
    border-radius: 0 0 10px 10px;
    padding: 10px;
    grid-gap: 15px;
    gap: 15px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
}

.card.done {
    -webkit-filter: contrast(0.5);
            filter: contrast(0.5);
}

.card.done::after {
    content: '';
    width: 100%;
    left: 0;
    top: 0;
    height: 100%;
    background-image: url(/react-todo-list/static/media/check-mark-emoji.f8612166.png );
    background-position: 50%;
    background-size: 25%;
    background-repeat: no-repeat;
    position: absolute;
    display: -webkit-flex;
    display: flex;
}

.card .topic {
    -webkit-filter: brightness(200%);
            filter: brightness(200%);
    color: black;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 5px 25px;
    border-radius: 10px;
}

.card footer {
    display: -webkit-flex;
    display: flex;
    grid-gap: 15px;
    gap: 15px;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.modal {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    grid-gap: 20px;
    gap: 20px;
    position: fixed;
    width: 400px;
    padding: 20px;
    background-color: white;
    z-index: 10;
    left: calc(50% - 400px/2);
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    top: 25%;
}

.modal input,
.modal textarea {
    padding: 5px;
    resize: none;
}

.modal .body {
    display: -webkit-flex;
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    -webkit-flex-flow: column;
            flex-flow: column;
}

.modal .body button {
    padding: 10px;
    margin-top: 10px;
    border: none;
    background-color: tomato;
    color: white;
}

.modal .top {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-size: 24px;
}


/* FOOTER */

#root>footer {
    display: -webkit-flex;
    display: flex;
    background-color: #1d1d1d;
}

#root>footer h3 {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 15px;
    gap: 15px;
    padding: 20px;
    color: white;
}

#root>footer a {
    text-transform: none;
    text-decoration: none;
}
@media screen and (max-width: 600px) {
    .card {
        max-width: unset;
    }
    .modal {
        width: 90%;
        left: 5%;
    }
}
